  padding: 12px 0 14px;
  text-transform: uppercase;
  text-align: center;
}

.whole-menu__main-link .ico,
.whole-menu__main-link svg {
  display: block;
  margin: 0 auto 8px;
}

.whole-menu__main-link:first-child {
  border-right: 1px solid #eaecf0;
}

.whole-menu__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  padding: 16px 12px 20px;
}

.whole-menu__list > li {
  padding-left: 12px;
}

.whole-menu__list > li:nth-child(even) {
  margin-left: 12px;
}

.whole-menu__list > li:nth-child(n+3) {
  margin-top: 12px;
}

.whole-menu__link {
  position: relative;
  padding-left: 16px;
}

.whole-menu__link::before {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 4px;
  height: 4px;
  border-radius: 50%;
  content: "";
  background-color: #344054;
}

.whole-menu__link:hover {
  text-decoration: underline;
}

.menu-item {
  display: block;
  padding: 12px;
  font-size: 0.875rem;
}

.menu-item__top {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 4px;
}

.menu-item__label {
  color: #0c111d;
