  font-weight: bold;
}

.menu-item__text {
  font-weight: regular;
  color: #667085;
  margin-bottom: 12px;
}

.menu-item__more {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: medium;
  color: #1570ef;
}

.menu-item:hover {
  border-radius: 8px;
  background-color: #f6f7f9;
}

.menu-item > *:last-child {
  margin-bottom: 0;
}

